















export default {
	name: "uni-grid",
	props: {
		data: Array, //数据
		type: { //布局格式，长方形oblong，正方形square
			type: String,
			default: 'square'
		},
		columnNum: { //每一行有多少个
			type: [Number, String],
			default: 3
		},
		showOutBorder: { //显示外边框
			type: [Boolean, String],
			default: true
		},
		showBorder: { //是否显示border，如果为false，showOutBorder无效
			type: [Boolean, String],
			default: true
		}
	},
	data() {
		return {}
	},
	computed: {
		columnNumber() {
			return Number(this.columnNum) ? Number(this.columnNum) : 3
		},
		gridGroup() {
			let group = []
			let groupItem = []
			this.data && this.data.forEach((item, index) => {
				groupItem.push(item)
				if (index % this.columnNum === this.columnNum - 1) {
					group.push(groupItem)
					groupItem = []
				}
			})
			if (groupItem.length > 0) {
				group.push(groupItem)
			}
			groupItem = null
			return group
		},
		setBorderClass() {
			let classList = []
			if (this.showBorder === false || this.showBorder === 'false') {
				classList.push('uni-grid-no-border')
				return classList
			}
			if (this.showOutBorder === false || this.showOutBorder === 'false') {
				classList.push('uni-grid-no-out-border')
			}
			return classList
		}
	},
	methods: {
		onClick(index, num) {
			this.$emit('click', {
				index: index * this.columnNumber + num
			})
		}
	}
}
